.waves {
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    flex-wrap: wrap;
    background-color: #fbf4e2;
    padding-top: 10px;
}

hr.solid1 {
    width: 100%;
    height: 3px;
    border: none;
    border-top: 3px solid #444;
}

.wavesGallery {
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: center;
}

.wavesImg {
    width: 120px;
    transition: transform 0.5s;
}

.wavesImg:hover {
    transform: scale(1.8, 1.8) translateY(-8px);
}

@media (max-width: 1250px) {

    .wavesImg {
        width: 80px;
    }
}

@media (max-width: 420px) { 

    .waves {
        display: none;
    }

}